.chat-home-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.home-header {
  text-align: center;
  padding: 20px 0;
  
  h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-size: 24px;
    font-weight: 600;
    margin: 0 0 10px 0;
    
    mat-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
    }
  }
  
  p {
    color: #666;
    margin: 0;
  }
}

.home-content {
  flex: 1;
  overflow-y: auto;
}

.new-chat-btn {
  width: 100%;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
  transition: transform 0.2s, box-shadow 0.2s;
  
  mat-icon {
    font-size: 24px;
    width: 24px;
    height: 24px;
  }
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  &:active {
    transform: translateY(0);
  }
}

.chat-history {
  h2 {
    font-size: 18px;
    font-weight: 600;
    margin: 0 0 15px 0;
    color: #333;
  }
}

.room-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.room-item {
  display: flex;
  align-items: center;
  padding: 15px;
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    background: #f5f5f5;
    border-color: #667eea;
  }
  
  .room-icon {
    position: relative;
    margin-right: 15px;
    
    mat-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
      color: #667eea;
    }
    
    .unread-badge {
      position: absolute;
      top: -5px;
      right: -5px;
      background: #ff4444;
      color: white;
      border-radius: 10px;
      padding: 2px 6px;
      font-size: 11px;
      font-weight: 600;
      min-width: 18px;
      text-align: center;
    }
  }
  
  .room-info {
    flex: 1;
    
    .room-status {
      font-size: 14px;
      font-weight: 500;
      color: #333;
      margin-bottom: 4px;
    }
    
    .room-time {
      font-size: 12px;
      color: #999;
    }
  }
  
  .arrow-icon {
    color: #ccc;
  }
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #999;
  
  mat-icon {
    font-size: 64px;
    width: 64px;
    height: 64px;
    margin-bottom: 20px;
    opacity: 0.3;
  }
  
  p {
    margin: 10px 0;
    font-size: 16px;
  }
  
  .empty-sub {
    font-size: 14px;
    color: #bbb;
  }
}
